

.My__box{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 50%;
    height: auto;
}
@media screen and (min-width:992px) {
    .custom{
        display: flex;
        justify-content: space-between;
        margin: 0px;
    }

}

@media screen and (max-width:992px){
    .My__box{
        width: 100%;
        position: relative;
    }
}