@import url(https://fonts.googleapis.com/css2?family=Baloo+Bhai+2:wght@500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.accordion-button::after{
  -webkit-filter: invert(1);
          filter: invert(1);
}
body{
  font-family: 'Baloo Bhai 2', cursive;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.blank{
  display: inline-block;
  color: rgb(1, 1, 58);
  border: 2px solid rgb(1, 1, 70);
  border-radius: 10px;
  padding: 20px;
  width: 40px;
  height: 20px;
  text-align: center;
  margin: 10rem;
  -webkit-transform: scew(2345px);
          transform: scew(2345px);
  -webkit-animation:blank infinite 8s linear;
          animation:blank infinite 8s linear;
}

@-webkit-keyframes blank {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes blank {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.My__box{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 50%;
    height: auto;
}
@media screen and (min-width:992px) {
    .custom{
        display: flex;
        justify-content: space-between;
        margin: 0px;
    }

}

@media screen and (max-width:992px){
    .My__box{
        width: 100%;
        position: relative;
    }
}
